.header {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  /* https://stackoverflow.com/a/13772549 */
  box-shadow: 0 1px 4px rgba(0, 21, 41, .08);
  position: relative;
  z-index: 10;

  padding: 0 16px;
  /* height: 50px;
    line-height: 50px; */
}

.logo a {
  display: flex;
  align-items: center;
}

.logo h1 {
  display: inline-block;
  margin: 0 0 0 12px;
  font-size: 20px;
  color: #1890ff;
  font-weight: 600;
}

.logo img {
  display: inline-block;
  width: 32px;
  height: 32px;
}

.body_content {
  margin: 0px;
  min-height: 280px;
  height: 100%;
  display: flex;
  flex-direction: column;
}