.login_container {
  width: 300px;
  margin: 0 auto;
}

.logo {
  max-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo h1 {
  margin: 0 0 0 12px;
  font-size: 20px;
  color: rgba(0, 0, 0, .45);
  margin-top: 12px;
  margin-bottom: 40px;
  text-align: center;
}

.logo img {
  height: 100px;
  margin-bottom: 20px;
}

.copyright {
  max-width: 300px;
  color: rgb(108, 117, 125);
  font-size: 16px;
  font-weight: 400;
  height: 24px;
  line-height: 24px;
  margin-bottom: 16px;
  margin-top: 48px;
  text-align: center;
  text-size-adjust: 100%;
}
