.container {
    width: 100%;
    height: 100%;
    font-weight: 300;
    margin: 0;
    padding: 0;
}

.title {
    padding-top: 30px;
    text-align: center;
    font-size: 40px;
    color: #48A9E6;
}

.circles {
    background: #fff;
    text-align: center;
    position: relative;
    margin-top: 20px;
}

.circles p {
    font-size: 240px;
    color: #fff;
    padding-top: 60px;
    position: relative;
    z-index: 9;
    line-height: 100%;
}

.circles p small {
    font-size: 40px;
    line-height: 100%;
    vertical-align: top;
}

/* animation: name .6s ease; // 这样是可以的
animation: .6s ease name; // 而这样就不会被识别到了 */
.circles .small {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    background: #48A9E6;
    position: absolute;
    z-index: 1;
    top: 80px;
    left: 50%;
    animation: smallmove 7s infinite cubic-bezier(1, .22, .71, .98);
    animation-delay: 1.2s;
}

.circles .med {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background: #48A9E6;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 10%;
    animation: medmove 7s infinite cubic-bezier(.32, .04, .15, .75);
    animation-delay: 0.4s;
}

.circles .big {
    width: 400px;
    height: 400px;
    border-radius: 50%;
    background: #48A9E6;
    position: absolute;
    z-index: 1;
    top: 200px;
    right: 0;
    animation: bigmove 8s infinite;
    animation-delay: 3s;
}

@keyframes smallmove {
    0% {
        top: 10px;
        left: 45%;
        opacity: 1;
    }
    25% {
        top: 300px;
        left: 40%;
        opacity: 0.7;
    }
    50% {
        top: 240px;
        left: 55%;
        opacity: 0.4;
    }
    75% {
        top: 100px;
        left: 40%;
        opacity: 0.6;
    }
    100% {
        top: 10px;
        left: 45%;
        opacity: 1;
    }
}

@keyframes medmove {
    0% {
        top: 0px;
        left: 20%;
        opacity: 1;
    }
    25% {
        top: 300px;
        left: 80%;
        opacity: 0.7;
    }
    50% {
        top: 240px;
        left: 55%;
        opacity: 0.4;
    }
    75% {
        top: 100px;
        left: 40%;
        opacity: 0.6;
    }
    100% {
        top: 0px;
        left: 20%;
        opacity: 1;
    }
}

@keyframes bigmove {
    0% {
        top: 0px;
        right: 4%;
        opacity: 0.5;
    }
    25% {
        top: 100px;
        right: 40%;
        opacity: 0.4;
    }
    50% {
        top: 240px;
        right: 45%;
        opacity: 0.8;
    }
    75% {
        top: 100px;
        right: 35%;
        opacity: 0.6;
    }
    100% {
        top: 0px;
        right: 4%;
        opacity: 0.5;
    }
}